// ä»å®ç½éé¢è·åcookie
const bear = 'Bearer '
const AUTH_TOKEN = UTIL.GetCookie('Authorization')
axios.defaults.headers.common['Authorization'] = bear + AUTH_TOKEN
axios.defaults.headers['Content-Type'] = 'application/json'
// å®ä¹axiosæ¦æªå¨ï¼å½cookieãå¤±ææ¶åå¯¼åç»éé¡µé¢

axios.interceptors.response.use(
  response => {
  //response.status===401æ¯æååå°çº¦å®çæéä¸¢å¤±æèæéä¸å¤è¿åçç¶æç ï¼è¿ä¸ªå¯ä»¥èªå·±ååå°çº¦å®ï¼çº¦å®è¿åæä¸ªèªå®ä¹å­æ®µä¹æ¯å¯ä»¥ç
    if (response.status === 401 || response.status === 404) {
      window.location.href = '/pages/login.html'
    }
    return response
  },
  error => {
    if (error.response.status === 401 || error.response.status === 404) {
      window.location.href = '/pages/login.html'
    }
    return Promise.reject(error.response.data)
  })

export default axios



